import './App.css';
import './css/fontawesome-free-6.2.1-web/css/all.css';
import Home from './Components/Home/Home';
import Navbar from './Components/Navbar/Navbar';
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import About from './Components/About/About';
import Project from './Components/Projects/Project';
import Python from './Components/Projects/Python/Python'
import Js from './Components/Projects/Js/Js';
import NodeJs from './Components/Projects/NodeJs/NodeJs';
import ReactJs from './Components/Projects/ReactJs/ReactJs';
import HTML from './Components/Projects/HTML/HTML';
import CSS from './Components/Projects/CSS/CSS';
import LoadingBar from 'react-top-loading-bar';
import CommingSoon from './Components/CommingSoon/CommingSoon';
import { useState } from 'react';
import ColorSettings from './Components/ColorSettings/ColorSettings';
import Blog from './Components/Blog/Blog';
import JavaScript from './Components/Blog/JavaScript/JavaScript';
import PythonCheat from './Components/Blog/Python/Python'
function App() {
  const [progress, setProgress] = useState(0)
  let [color, setColor] = useState('')
  let [value, setvalue] = useState(false)
  const toggleMode = (cls,isvalue) => {
    setColor(cls)
    setvalue(isvalue)
  }
  return (
    <>
      <div className="body">
        <Router basename='/zer0ne'>
          <Navbar mode={color} isValue={value} />
          <ColorSettings mode={color} isValue={value} toggleMode={toggleMode} />
          <LoadingBar color='#f11946'
            progress={progress} />
          <Routes>
            <Route exact path="/zer0ne/" element={<Home mode={color} isValue={value} setProgress={setProgress} />} />
            <Route path="/zer0ne/about" element={<About mode={color} isValue={value} setProgress={setProgress} />} />
            <Route path="/zer0ne/project" element={<Project mode={color} isValue={value} setProgress={setProgress} />} />
            <Route path="/zer0ne/project/python" element={<Python mode={color} isValue={value} setProgress={setProgress} />} />
            <Route path="/zer0ne/project/js" element={<Js mode={color} isValue={value} setProgress={setProgress} />} />
            <Route path="/zer0ne/project/nodejs" element={<NodeJs mode={color} isValue={value} setProgress={setProgress} />} />
            <Route path="/zer0ne/project/reactjs" element={<ReactJs mode={color} isValue={value} setProgress={setProgress} />} />
            <Route path="/zer0ne/project/html" element={<HTML setProgress={setProgress} />} />
            <Route path="/zer0ne/project/css" element={<CSS setProgress={setProgress} />} />
            <Route path='/zer0ne/blog' element={<Blog mode={color} isValue={value} />} />
            <Route path='/zer0ne/blog/javascript-cheetsheet' element={<JavaScript setProgress={setProgress} mode={color} isValue={value} />} />
            <Route path='/zer0ne/blog/python-cheetsheet' element={<PythonCheat setProgress={setProgress} mode={color} isValue={value} />} />
            {/* <Route path='/zer0ne/blog' element={<CommingSoon />} /> */}
            <Route path='/zer0ne/contact' element={<CommingSoon />} />
          </Routes>
        </Router>
      </div>
    </>
  );
}
export default App;