import React from 'react'
import '../Blog.css'
import copy from "copy-to-clipboard";
import { useState } from 'react';
// import SyntaxHighlighter from 'react-syntax-highlighter';
// import { solarizedDark } from 'react-syntax-highlighter/dist/esm/styles/hljs';
export default function CodeDiv(props) {
    const [copyText, setCopyText] = useState('');
    const copyToClipboard = () => {
        setCopyText(props.code);
        copy(copyText);
    }
    return (
        <>
            <div className="code" style={props.headerStyle}>
                < div className="header" >
                    <div onClick={copyToClipboard} className="copy">
                        {/* copy */}
                    </div>
                </div>
                <div className="codeDiv" style={props.style}>
                    <pre className="blogContent">
                        {/* <SyntaxHighlighter
                            language={props.language}
                            style={solarizedDark}
                            customStyle={{
                                background: "transparent",
                                opacity: "1",
                                color: 'white',
                            }}
                        >
                            {props.code}
                        </SyntaxHighlighter> */}
                        <code>
                            {props.code}
                        </code>
                    </pre>
                </div>
            </div>
        </>
    )
}
